@font-face {
  font-family: "Graphit";
  src: local("Custom Font"), url("../fonts/Graphit-Regular.eot"); /* IE9 Compat Modes */
  src: url("../fonts/Graphit-Regular.eot?#iefix") format("embedded-opentype"),
    /* IE6-IE8 */ url("../fonts/Graphit-Regular.woff2") format("woff2"),
    /* Super Modern Browsers */ url("../fonts/Graphit-Regular.woff")
      format("woff"),
    /* Pretty Modern Browsers */ url("../fonts/Graphit-Regular.ttf")
      format("truetype"),
    /* Safari, Android, iOS */ url("../fonts/Graphit-Regular.svg#svgFontName")
      format("svg"); /* Legacy iOS */
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  unicode-range: U+000-5FF;
}

:root {
  --color-text: #fff;
  --color-bg: #111;
  --color-link: #aaa;
  --color-link-hover: #fff;
  --color-alt: #7f6a57;
}

body {
  margin: 0;
  color: var(--color-text);
  background-color: var(--color-bg);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  counter-reset: contentSection;
  font-family: graphit, sans-serif;
  font-size: 16px;
}

/* Page Loader */
.js .is-loading::before,
.js .is-loading::after {
  content: "";
  position: fixed;
  z-index: 1000;
}

.js .is-loading::before {
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: var(--color-bg);
}

.js .is-loading::after {
  top: 50%;
  left: 50%;
  width: 60px;
  height: 60px;
  margin: -30px 0 0 -30px;
  border-radius: 50%;
  opacity: 0.4;
  background: var(--color-link);
  animation: loaderAnim 0.7s linear infinite alternate forwards;
}

@keyframes loaderAnim {
  to {
    opacity: 1;
    transform: scale3d(0.5, 0.5, 1);
  }
}

a {
  text-decoration: underline;
  color: var(--color-link);
  outline: none;
}

a:hover,
a:focus {
  color: var(--color-link-hover);
  outline: none;
  text-decoration: none;
}

main {
  overflow: hidden;
  position: relative;
}

.header-container {
  text-align: center;
  position: relative;
  z-index: 900;
}

.header-title {
  font-size: 1rem;
  margin: 0 0 1rem;
  font-weight: normal;
}

.content {
  padding: 3rem;
  min-height: 90vh;
  display: flex;
  position: relative;
  flex-direction: column;
  align-content: center;
  justify-content: space-between;
}

.content-auto {
  min-height: 0;
}

.content-feature {
  justify-content: flex-start;
  min-height: 0;
}

.content-fixed {
  position: fixed;
  z-index: 1000;
  width: 100%;
  top: 0;
}

.content-pretitle {
  margin-top: auto;
  font-size: 1.85rem;
  color: var(--color-alt);
}

.content-pretitle::before {
  content: "\2015";
  margin-right: 0.25rem;
}

.content-title {
  font-size: 9vw;
  line-height: 0.8;
  margin: 0 0 3rem 0;
  font-weight: 400;
  max-width: 800px;
  will-change: transform;
}

.content-text {
  font-size: 2.25rem;
  font-size: clamp(1rem, 4vw, 2.25rem);
}

.content-text-right {
  align-self: flex-end;
}

.content-text-centered {
  margin: auto;
}

.content-breakout {
  margin: 0 -3rem;
  white-space: nowrap;
  text-transform: uppercase;
  font-family: moret, serif;
  font-weight: 200;
  font-style: italic;
  color: #2c2a26;
}

.content-breakout-l {
  font-size: 13vw;
  font-weight: 200;
}

.content-breakout-m {
  font-size: 5vw;
}

.s-tiles {
  --tiles-height: 52vw;
  height: var(--tiles-height);
  position: relative;
  overflow: hidden;
}

.tiles-columns-rotated {
  --tiles-height: 180vmax;
}

.tiles-columns {
  --tiles-height: 140vh;
}

.tiles-oneline {
  --tiles-height: 50vh;
  min-height: 400px;
  margin: 10vh 0;
}

.tiles-r {
  --tiles-height: 100px;
  margin: 10vh 0 0;
}

.tiles-fixed {
  --tiles-height: 65vw;
  margin-top: 25vh;
}

.tiles-perspective {
  overflow: visible;
  perspective: 1000px;
  --tiles-height: 500px;
}

.tiles-container {
  width: 150%;
  --tileswrap-height: var(--tiles-height);
  height: var(--tileswrap-height);
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate3d(-50%, -50%, 0);
}

.tiles-rotated .tiles-container {
  --tileswrap-height: calc(var(--tiles-height) * 1.8);
  transform: translate3d(-50%, -50%, 0) rotate(22.5deg);
}

.tiles-columns-rotated .tiles-container {
  width: 150%;
  display: flex;
  transform: translate3d(-50%, -50%, 0) rotate(-22.5deg);
  left: 50%;
  top: 50%;
  align-items: center;
  justify-content: center;
}

.tiles-columns .tiles-container {
  width: 100%;
  display: flex;
  --tileswrap-height: calc(var(--tiles-height) * 1.4);
  transform: translate3d(0, -50%, 0);
  left: 0;
  top: 50%;
}

.tiles-perspective .tiles-container {
  width: 200%;
  transform: translate3d(-50%, -50%, 0) translateX(-25%) translateZ(-1200px)
    rotateX(75.5deg) rotateZ(12deg);
}

.tiles-fixed .tiles-container {
  width: 100%;
}

.tiles-line {
  display: flex;
}

.tiles-columns .tiles-line {
  width: 25%;
  padding: 0 1rem;
  display: block;
}

.tiles-columns-rotated .tiles-line {
  width: 320px;
  flex: none;
  padding: 0 1.5vw;
  display: block;
}

.tiles-columns-rotated .tiles-line:nth-child(even) {
  margin-top: -160px;
}

.tiles-oneline .tiles-line {
  height: 100%;
}

.tiles-r .tiles-line {
  height: 100%;
}

.tiles-line-img {
  --tile-margin: 2vw;
  flex: none;
  width: calc(16.6666% - var(--tile-margin) * 2);
  height: calc(var(--tileswrap-height) / 3 - (3 * var(--tile-margin) / 2));
  margin: var(--tile-margin);
  background-size: cover;
  background-position: 50% 50%;
}

.tiles-perspective .tiles-line-img {
  backface-visibility: hidden;
  outline: 1px solid transparent;
  --tile-margin: 1vw;
  width: calc(16.6666% - var(--tile-margin) * 2);
  height: calc(16.666vw * 1.3);
}

.tiles-oneline .tiles-line-img {
  --tile-margin: 1vw;
  margin: 0 var(--tile-margin);
  width: calc(25% - var(--tile-margin) * 2);
  height: 100%;
}

.tiles-r .tiles-line-img {
  --tile-margin: 5px;
  margin: 0 var(--tile-margin);
  width: calc(12.5% - var(--tile-margin) * 2);
  height: 100%;
}

.tiles-rotated .tiles-line-img {
  --tile-margin: 1vw;
  width: calc(16.6666% - var(--tile-margin) * 2);
  height: calc(var(--tileswrap-height) / 5 - (4 * var(--tile-margin) / 2));
}

.tiles-columns-rotated .tiles-line-img {
  --tile-margin: 3vw 0;
  width: 100%;
  height: 400px;
}

.tiles-columns .tiles-line-img {
  --tile-margin: 2rem 0;
  width: 100%;
  height: calc(25vw * 1.3);
}

.tiles-fixed .tiles-line-img {
  --tile-margin: 10px;
  -webkit-filter: brightness(0.8);
  width: calc(16.6666% - var(--tile-margin) * 2);
  height: calc(var(--tileswrap-height) / 3 - (3 * var(--tile-margin) / 2));
}

.tiles-darker .tiles-line-img {
  opacity: 0.7;
}

.tiles-title {
  position: absolute;
  height: 100vh;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 10vw;
  padding: 3rem;
  margin: 0;
  line-height: 0.8;
  font-family: moret, serif;
  font-weight: 700;
  font-style: normal;
}

.tiles-title-alt {
  font-size: 9vw;
  font-size: clamp(2rem, 9vw, 7.25rem);
}

.tiles-title-intro {
  padding-top: 10vh;
  align-items: flex-start;
  justify-content: flex-start;
}

.tiles-title-right {
  justify-content: flex-end;
}

.tiles-title-left {
  justify-content: flex-start;
}

.tiles-title-full {
  height: 100%;
}

.backtop {
  align-self: center;
  margin: auto 0;
  font-size: 6vw;
  cursor: pointer;
  text-decoration: none;
}

.backtop::after {
  content: "\2934";
  font-size: 3vw;
  vertical-align: top;
  margin-left: 1rem;
}

/*! locomotive-scroll v3.5.4 | MIT License | https://github.com/locomotivemtl/locomotive-scroll */
html.has-scroll-smooth {
  overflow: hidden;
}

html.has-scroll-dragging {
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.has-scroll-smooth body {
  overflow: hidden;
}

.has-scroll-smooth [data-scroll-container] {
  min-height: 100vh;
}

.c-scrollbar {
  position: absolute;
  right: 0;
  top: 0;
  width: 11px;
  height: 100vh;
  transform-origin: center right;
  transition: transform 0.3s, opacity 0.3s;
  opacity: 0;
}
.c-scrollbar:hover {
  transform: scaleX(1.45);
}
.c-scrollbar:hover,
.has-scroll-scrolling .c-scrollbar,
.has-scroll-dragging .c-scrollbar {
  opacity: 1;
}

.c-scrollbar_thumb {
  position: absolute;
  top: 0;
  right: 0;
  background-color: black;
  opacity: 0.5;
  width: 7px;
  border-radius: 10px;
  margin: 2px;
  cursor: -webkit-grab;
  cursor: grab;
}
.has-scroll-dragging .c-scrollbar_thumb {
  cursor: -webkit-grabbing;
  cursor: grabbing;
}

@media screen and (min-width: 768px) {
  .header-container {
    display: grid;
    align-content: space-between;
    width: 100%;
    grid-gap: 5vw;
    grid-template-columns: auto auto auto 1fr;
    grid-template-areas: "title title links demos";
  }
  .header-title {
    margin: 0;
    grid-area: title;
  }
  .tiles-columns-rotated .tiles-line {
    width: 33vmax;
  }
  .tiles-columns-rotated .tiles-line:nth-child(even) {
    margin-top: -20vmax;
  }
  .tiles-columns-rotated .tiles-line-img {
    height: 40vmax;
  }
  .content-numbered::after {
    counter-increment: contentSection;
    content: counter(contentSection, decimal-leading-zero);
    position: absolute;
    top: 50%;
    right: 3rem;
    border: 1px solid;
    color: var(--color-alt);
    width: 4rem;
    height: 4rem;
    margin-top: -2rem;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
  }
  .content-text {
    max-width: 800px;
    width: 50vw;
    min-width: calc(300px - 6rem);
  }
  .content-text-wide {
    max-width: 1000px;
    width: 65vw;
  }
  .tiles-r {
    --tiles-height: 240px;
  }
}
